import React, { useEffect, useState, useMemo } from 'react';
import { Table, Button, Container, Row, Col, Modal, InputGroup, FormLabel, Pagination } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import groupLoan from '../../models/groupLoan';
import Loader from '../layout/Loader';
import DatePicker from "react-datepicker";
import moment from "moment";
import { useTable, useSortBy, usePagination, useFilters } from 'react-table';
import { useReactToPrint } from 'react-to-print';
import { useRef } from 'react';
import PrintDueEmis from './printDueEmis';
function DueEmis(props) {
    const [showDeleteModel, setShowDeleteModel] = useState(false)
    const [isShowLoader, setisShowLoader] = useState(false)
    const [dueEmis, setDueEmis] = useState([]);
    const [paidID, setPaidID] = useState(0)
    const [enrollmentDate, setEnrollmentDate] = useState(new Date());
    const emiDetailsRef = useRef();
    const handlePrintEmiDetail = useReactToPrint({
        content: () => emiDetailsRef.current,
        documentTitle:"AA2-Due-EMIs",
      });

    useEffect(() => {
        getDueEmisRecord();
    }, [])

    const data = useMemo(
        () =>
            dueEmis.map((record, index) => {
                return {
                    col1: record.loan_account_no,
                    col2: record.member_group_id,
                    col3: record.village_name,
                    col4: record.member_id,
                    col5: record.member_name,
                    col6: record.remain_EMI,
                    col7: record.EMI_amount.toFixed(2),
                    col8: record.outstanding,
                    col9: record,
                }
            }
            )
        ,
        [dueEmis]
    )

    const columns = useMemo(
        () => [
            {
                Header: 'Account no.',
                accessor: 'col1', // accessor is the "key" in the data
                allowFilter: true,
                Cell: e => { return(<Link to={{
                    pathname: '/loanApprovalDetails',
                    state: dueEmis[e.row.id]?.loan_table_id
                }}>{e.value}</Link>)}
            },
            {
                Header: 'Group',
                accessor: 'col2', // accessor is the "key" in the data
                allowFilter: true,
                 Cell: e => <Link to="/memberGroup">{e.value}</Link>
            },
            {
                Header: 'Village Name',
                accessor: 'col3', // accessor is the "key" in the data
                allowFilter: true,
                 Cell: e => <Link to="/village">{e.value}</Link>
            },
            {
                Header: 'Member ID',
                accessor: 'col4',
                allowFilter: true,
                Cell: emi => {
                    return (<Link to={{
                    pathname: '/viewMemberDetail',
                    state: emi.value
                }}>{emi.value}</Link>)}
            },
            {
                Header: 'Name',
                accessor: 'col5',
                allowFilter: true,
                Cell: emi => <Link to={{
                    pathname: '/viewMemberDetail',
                    state:dueEmis[emi.row.id]?.member_id
                }}>{emi.value}</Link>
            },
            {
                Header: 'No. of EMI',
                accessor: 'col6',
                allowFilter: true
            },
            {
                Header: 'EMI Amount',
                accessor: 'col7',
                allowFilter: true
            },
            {
                Header: 'Outstanding',
                accessor: 'col8',
                allowFilter: false
            },
            {
                Header: 'Action',
                accessor: 'col9',
                allowFilter: false,
                Cell: emi => <Button size={"sm"} variant="success" onClick={() => paidHandle(emi.value.emi_id)}>Pay</Button>
            },
        ],
        [dueEmis]
    )
    function DefaultColumnFilter({
        column: { filterValue, preFilteredRows, setFilter },
    }) {
        const count = preFilteredRows.length

        return (
            <input
                className="form-control"
                value={filterValue || ''}
                onChange={e => {
                    setFilter(e.target.value || undefined)
                }}
                placeholder={`Search ${count} records...`}
            />
        )
    }
    const defaultColumn = React.useMemo(
        () => ({
            // Default Filter UI
            Filter: DefaultColumnFilter,
        }),
        []
    )
    const paginate = (totalPage) => {
        let pageNumberArray = [];
        for (let i = 1; i <= totalPage; i++) {
            pageNumberArray.push(i);
        }
        return (
            <Pagination className="text-left">
                <Pagination.First onClick={() => gotoPage(0)} />
                <Pagination.Prev onClick={() => previousPage()} disabled={!canPreviousPage} />
                {pageNumberArray.map(num => <Pagination.Item key={num} active={num === (pageIndex + 1)} onClick={() => gotoPage(num - 1)}>{num}</Pagination.Item>)}
                <Pagination.Next onClick={() => nextPage()} disabled={!canNextPage} />
                <Pagination.Last onClick={() => gotoPage(totalPage - 1)} />
            </Pagination>
        );

    }

    const getDueEmisRecord = (today = moment().format("yyyy-MM-DD")) => {
        setisShowLoader(true);
        groupLoan.EmiModel.getDueEmis(today).then(res => {
            setisShowLoader(false);
            if (res.statusCode == 200) {
                let emiData = [];
                emiData = res.body.message.map(emi => {
                    // return emi
                    emi["loan_table_id"] = emi.loan_account_no.substring(11)
                    return emi;
                });
                setDueEmis(emiData);
            } else {
                setDueEmis([]);
            }
        })
    }
    const paidEMI = async () => {
        setShowDeleteModel(false);
        try {
            setisShowLoader(true);
            let response = await groupLoan.EmiModel.paidEmi(paidID);
            setisShowLoader(false);
            if (response.statusCode == 200) {
                let UpdatedList = dueEmis.filter((emidata, idx) => emidata.emi_id != paidID);
                setDueEmis(UpdatedList);
            }
        } catch (error) {
            console.log(error.response.body.message);
            setisShowLoader(false);
        }
    }
    const paidHandle = (id) => {
        setPaidID(id);
        setShowDeleteModel(true);
    }
    const changeEmiDate = (selectedDate) => {
        setEnrollmentDate(selectedDate)
        getDueEmisRecord(moment(selectedDate).format("yyyy-MM-DD"));
    }
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        pageOptions,
        page,
        pageCount,
        state: { pageIndex, pageSize },
        gotoPage,
        previousPage,
        nextPage,
        setPageSize,
        canPreviousPage,
        canNextPage,
    } = useTable({ columns, data, defaultColumn, initialState: { pageIndex: 0 } }, useFilters, useSortBy, usePagination)
    return (
        <>
            <Modal show={showDeleteModel} onHide={() => setShowDeleteModel(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Paid EMI </Modal.Title>
                </Modal.Header>
                <Modal.Body>Are you sure?</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() => setShowDeleteModel(false)}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={() => paidEMI()}>
                        Paid
                    </Button>
                </Modal.Footer>
            </Modal>

            <Loader show={isShowLoader} />
            <Container fluid>
                <h2 className="text-info text-center">Due EMIs</h2>
                <Row>
                    <Col>
                        <InputGroup className="mb-3">
                            <FormLabel className="mr-3">EMI Date:</FormLabel>
                            <DatePicker class="form-control"
                                selected={enrollmentDate}
                                onChange={(date) => changeEmiDate(date)}
                                name="enrollment_date"
                                dateFormat="dd/MM/yyyy"
                            />

                        </InputGroup>

                        <div>
                            {paginate(pageCount)}
                            <svg style={{cursor:'pointer'}} onClick={handlePrintEmiDetail} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer mb-2" viewBox="0 0 16 16">
            <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
            <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
            </svg>
                        </div>
                        <Table  {...getTableProps()} className="shadow-lg p-3 mb-5 bg-white rounded small" striped bordered hover responsive>
                            <thead className="bg-primary">
                                {headerGroups.map(headerGroup => (
                                    <tr {...headerGroup.getHeaderGroupProps()}>
                                        {headerGroup.headers.map(column => (
                                            <th
                                                {...column.getHeaderProps(column.getSortByToggleProps())}
                                            >
                                                {column.render('Header')}
                                                <span>
                                                    {column.isSorted ? (column.isSortedDesc ? ' ' : ' ') : ''}
                                                </span>
                                                <div>{column.allowFilter ? column.render('Filter') : null}</div>
                                            </th>
                                        ))}
                                    </tr>
                                ))}
                            </thead>
                            <tbody{...getTableBodyProps()}>

                                {page.map(row => {
                                    prepareRow(row)
                                    return (
                                        <tr {...row.getRowProps()}>
                                            {row.cells.map(cell => {
                                                //console.log(cell);
                                                return (
                                                    <td {...cell.getCellProps()}>
                                                        {cell.render('Cell')}
                                                    </td>
                                                    )

                                            })}
                                        </tr>
                                    )
                                })}

                            </tbody>
                        </Table>
                        <div style={{display:"none"}}> <PrintDueEmis ref={emiDetailsRef} dueEmis={rows}/></div>
                        <div className="text-right">
                            {paginate(pageCount)}
                        </div>


                    </Col>
                </Row>
            </Container>
        </>
    );
}

export default DueEmis;