import React from 'react';
import { Table } from 'react-bootstrap';

class PrintDueEmis extends React.PureComponent { 
    add =(row,a)=>{
        console.log(row,a.values);
        return parseFloat(a.values.col7) + row;
    }
    render() {
    return (
        <div>
            <h3 className="text-center mb-5 mt-3">AAs2 MutualHelp Micro Finance</h3>
            <Table className=" shadow-lg p-3 mb-5 bg-white rounded small" striped bordered hover responsive>
                <thead className="bg-primary">
                    <tr>
                        <th>Account No.</th>
                        <th>Group</th>
                        <th>Village Name</th>
                        <th>Member ID</th>
                        <th>Name</th>
                        <th>No. of Emi</th>
                        <th>EMI Amount</th>
                        <th>Outstanding</th>
                        <th>Company Signature</th>
                        <th>Customer Signature</th>
                    </tr>
                </thead>
                <tbody>
                    {this.props.dueEmis.map((emi, id) => (<tr>
                        <td>{emi.values.col1}</td>
                        <td>{emi.values.col2}</td>
                        <td>{emi.values.col3}</td>
                        <td>{emi.values.col4}</td>
                        <td>{emi.values.col5}</td>
                        <td>{emi.values.col6}</td>
                        <td>{emi.values.col7}</td>
                        <td>{emi.values.col8}</td>
                        <td className="border"></td>
                        <td className="border"></td>
                    </tr>))}
                </tbody>
                <tfoot>
                <tr>
                <th>Total Collection</th><th colSpan="9">{this.props.dueEmis.reduce(this.add,0)}</th>
                    
                </tr>
                </tfoot>
            </Table> 
            
        </div>
    );
    }
}

export default PrintDueEmis;