import React, { useEffect, useState } from 'react';
import { Table,Button, Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import groupLoan from "../../models/groupLoan";
import Loader from '../layout/Loader';
import moment from 'moment';
function Village(props) {
    const [isShowLoader, setisShowLoader] = useState(false)
const [villageRecords,setVillageRecords]=useState([]);
    useEffect(() => {
            getVillages();
    },[])

    const getVillages = () =>{
        setisShowLoader(true);
        groupLoan.villageModel.getVillage().then(res=>{
            setisShowLoader(false);
            if(res.statusCode == 200){
                setVillageRecords(res.body.message);
            }else{
                setVillageRecords([]);
            }
        })
    }
    
    return (
        <>
        <Loader show={isShowLoader}/>
        <Container fluid>
        <h2 className="text-info text-center">Village</h2>
            <Row className="mb-5 mt-3">
                <Col>
                <Link to="/addVillage">
                <Button variant="info" active>
                    Add Village
                </Button>
                </Link>
                </Col>
            </Row>
            <Row>
            <Col>
            <Table className=" shadow-lg p-3 mb-5 bg-white rounded" striped bordered hover responsive>
            <thead className="bg-primary">
                <tr>
                    <th>Village ID</th>
                    <th>Village Name</th>
                    <th>Week</th>
                    <th>Day</th>
                </tr>
            </thead>
            <tbody>
                {villageRecords.map((vill,id)=>(<tr>
                    <td>{vill.id}</td>
                    <td>{vill.village_name}</td>
                    <td>{vill.week}</td>
                    <td>{vill.day }</td>
                </tr>))}
            </tbody>
        </Table>
</Col>
            </Row>
        </Container>
        </>
        );
}

export default Village;