import React, { useState } from 'react';
import { Button, Form, Container, Row, Col, Toast } from 'react-bootstrap';
import { useFormik } from 'formik';
import groupLoan from "../../models/groupLoan";
import Loader from '../layout/Loader';
import { useSelector } from "react-redux";

function AddVillage(props) {
  const [values, setValues] = useState({});
  const [isShowLoader, setisShowLoader] = useState(false)
  const [showToast, setShowToast] = useState({ isShow: false,type:"", message: "" })
  const validate = values => {
    const errors = {};
    if (!values.village_name) {
      errors.village_name = 'Village name is Required';
    }
    if (!values.week) {
        errors.week = 'Week is Required';
    }
    if (!values.day) {
    errors.day = 'Day is Required';
    }
    return errors;
  };
  const formik = useFormik({
    initialValues: {
      village_name: '',
      week: '',
      day: '',
    },
    validate,
    onSubmit: async (values) => {
      setisShowLoader(true);
      try {
        let response = await groupLoan.villageModel.saveVillage(values);
        setisShowLoader(false);
        if (response.statusCode == 200) {
          setShowToast({ isShow: true,type:"bg-success", message: response.body.message })
          formik.resetForm();
        } else {
          setShowToast({ isShow: true,type:"bg-danger", message: response.body.message })
        }

      } catch (error) {
        setisShowLoader(false);
        setShowToast({ isShow: true,type:"bg-danger", message: error.response.body.message })
      }
    },
    handleChange: () => {
      setValues(prevValues => ({
        ...prevValues,
        // we use the name to tell Formik which key of `values` to update
        [values.target.name]: values.target.value
      }))
    },
  });
  return (
    <>
      <Toast key={1} autohide delay={3000} show={showToast.isShow} onClose={() => setShowToast({ isShow: false,type:"", message: "" })} className={"loader "+ (showToast.type) } >
        <Toast.Header>
          <strong className="me-auto">{showToast.type=="bg-danger"?"Error":"Success"} Message</strong>
        </Toast.Header>
        <Toast.Body className="Dark">
        {showToast.message}
        </Toast.Body>
      </Toast>

      <Loader show={isShowLoader} />
      <h2 className="text-info text-center">Add Village</h2>
      <Row>
        <Col md={6}>
          <Container fluid className="bg-white mt-5 shadow-lg p-3 mb-5 bg-white rounded">
            <Form onSubmit={formik.handleSubmit}>
              <Form.Group className="mb-3 form-group required" controlId="formBasicEmail">
                <Form.Label>Village Name</Form.Label>
                <Form.Control
                  type="text"
                  name="village_name"
                  placeholder="Enter village name"
                  onChange={formik.handleChange}
                  value={formik.values.village_name}

                />
                {formik.touched.village_name && formik.errors.village_name ? (
                  <div class="text-danger">{formik.errors.village_name}</div>
                ) : null}

              </Form.Group>

              <Form.Group as={Col} className="mb-3 pl-0 pr-0 required" controlId="formGridAddress1">
                <Form.Label>Week</Form.Label>
                <select
                    className="form-control"
                    name="week"
                    onChange={formik.handleChange}
                    value={formik.values.week}>
                    <option key="" value="">Select Week</option>
                    <option key="1" value="1">Week 1</option>
                    <option key="2" value="2">Week 2</option>
                </select>
                {formik.touched.week && formik.errors.week ? (
                  <div class="text-danger">{formik.errors.week}</div>
                ) : null}
            </Form.Group>
            <Form.Group as={Col} className="mb-3 pl-0 pr-0 required" controlId="formGridAddress1">
                <Form.Label>Day</Form.Label>
                <select
                    className="form-control"
                    name="day"
                    onChange={formik.handleChange}
                    value={formik.values.day}>
                    <option key="" value="">Select day</option>
                    <option key="monday" value="monday">Monday</option>
                    <option key="tuesday" value="tuesday">Tuesday</option>
                    <option key="wednesday" value="wednesday">Wednesday</option>
                    <option key="thrusday" value="thrusday">Thrusday</option>
                    <option key="friday" value="friday">Friday</option>
                </select>
                {formik.touched.day && formik.errors.day ? (
                  <div class="text-danger">{formik.errors.day}</div>
                ) : null}

            </Form.Group>
              <Button variant="primary" type="submit">
                Save
              </Button>

            </Form>
          </Container>
        </Col>
      </Row>
    </>

  );
}

export default AddVillage;